:global(*) {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:global(body) {
  font-family: Arial, sans-serif;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #031671; 
  position: relative;
  overflow: hidden;
}

:global(body::before), :global(body::after) {
  content: '';
  position: absolute;
  top: -150px;
  width: 300px;
  height: 300px;
  background: rgba(255, 255, 255, 0.2); 
  border-radius: 50%;
  animation: bubbleMovement 15s infinite ease-in-out;
  z-index: -1;
}

:global(body::after) {
  left: 50%;
  animation-duration: 12s;
  animation-delay: 3s;
  background: rgba(255, 255, 255, 0.1); 
}

@keyframes bubbleMovement {
  0% {
    transform: translateY(0) scale(1);
  }
  50% {
    transform: translateY(-500px) scale(1.2);
  }
  100% {
    transform: translateY(0) scale(1); 
  }
}

.form h1 {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.form input {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border-radius: 10px;
  border: none;
  outline: none;
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  font-size: 16px;
}

.form input::placeholder {
  color: #ccc;
}

.form button {
  width: 100%;
  padding: 12px;
  background-color: #007BFF;
  border: none;
  border-radius: 10px;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
  box-shadow: 0 0 15px rgba(0, 123, 255, 0.7); 
}

.form button:hover {
  background-color: #0056b3;
}


.form div{
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px); 
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
  text-align: center;
  color: #fff;
  width: 350px;
  height: 400px; 
  flex-direction: column;
  justify-content: center;
}

.aviso {
  color: red;
  background-attachment: fixed;
  background-color: #ccc;
}